import React from "react";
import "../Header/header.scss";
import LogoSVG from "../../assets/batman.svg";
import { HandySvg } from "handy-svg";
const Header = ({ checked, setChecked }) => {
  const cls = ["header___container__checkboxes_lbl_inp"];
  if (checked) {
    cls.push("blue");
  }
  return (
    <header className="header">
      <div className="header___container">
        <div className="header___container__logo">
          {/* <LogoSVG
            width="6rem"
            height="6rem"
            className="header___container__logo_name"
            
          /> */}
          <HandySvg
            src={LogoSVG}
            className="header___container__logo_name"
            width="6rem"
            height="6rem"
          />
        </div>
        <div className="header___container__checkboxes">
          <label className="header___container__checkboxes_lbl">
            <p>Цвет фона</p>
            <input
              type="checkbox"
              checked={checked}
              onChange={() => setChecked(!checked)}
            />
            <span className={cls.join(" ")}>
              <i className="header___container__checkboxes_lbl_inp_circle"></i>
            </span>
          </label>
        </div>
      </div>
    </header>
  );
};

export default Header;
